<template lang="html">
  <div class="page" ref="popWrapper">
    <div class="content">
<a name="process198"></a>

<p><br></p>
<p><span class="rvts17">生存危机（</span><span class="rvts50">An Existential Crisis</span><span class="rvts17">）</span><span class="rvts50">[</span><span class="rvts17">支线</span><span class="rvts50">]</span><span class="rvts17">：</span></p>
<p><span class="rvts41">坐标：</span>
<br><span class="rvts20">X:627,Y:154</span><span class="rvts21">，</span><span class="rvts20">NPC</span><span class="rvts21">：克雷斯品（</span><span class="rvts20">Crispin</span><span class="rvts21">）</span>
<br><span class="rvts39"></span>
<br><span class="rvts41">目标：</span>
<br><span class="rvts21">答对博学者克雷斯品的问题，获得大奖</span>
<br><span class="rvts39"></span>
<br><span class="rvts41">任务详情：</span>
<br><img width="100%" height="auto" alt="" hspace="1" vspace="1" src="../../../assets/pic2/abcamber1236.jpg" display="block"><span class="rvts39"></span>
<br><span class="rvts21">在墓地花园的一个很偏的台阶处，可以不断听到一处求救的声音，走近会发现这里是博学者克雷斯品的坟墓。与他交谈，全部通过他提出的</span><span class="rvts20">3</span><span class="rvts21">个问题，便可以获得大大的奖励。</span>
<br><span class="rvts21">当然，强烈建议读了以下内容后再与它交谈，因为如果一旦未通过测试，那么等来的只有瞬间死亡。</span>
<br><span class="rvts21">该测试可以通过的方式有：</span>
<br><span class="rvts21">第一道题：使用</span><span class="rvts20"></span><span class="rvts21">小丑（</span><span class="rvts20">Jester</span><span class="rvts21">）</span><span class="rvts20"></span><span class="rvts21">或</span><span class="rvts20"></span><span class="rvts21">野蛮人（</span><span class="rvts20">Babarian</span><span class="rvts21">）</span><span class="rvts20"></span><span class="rvts21">标签，或读过《生命的本质：第一卷》内容，便可以通过。</span>
<br><span class="rvts21">第二道题：使用</span><span class="rvts20"></span><span class="rvts21">神秘（</span><span class="rvts20">Mystic</span><span class="rvts21">）</span><span class="rvts20"></span><span class="rvts21">或</span><span class="rvts20"></span><span class="rvts21">法外狂徒（</span><span class="rvts20">Outlaw</span><span class="rvts21">）</span><span class="rvts20"></span><span class="rvts21">标签，或读过《生命的本质：第二卷》内容，便可以通过。</span>
<br><span class="rvts21">第三道题：使用</span><span class="rvts20"></span><span class="rvts21">学者</span><span class="rvts20"></span><span class="rvts21">天赋，或读过《生命的本质：第三卷》内容，便可以通过。</span>
<br><span class="rvts39"></span>
<br><span class="rvts21">另外，如果队伍中有不死族的角色，可以使用</span><span class="rvts20"></span><span class="rvts21">不死族（</span><span class="rvts20">Undead</span><span class="rvts21">）</span><span class="rvts20"></span><span class="rvts21">特定的标签语句回答，通过所有的问题。</span>
<br><span class="rvts21">回答对第一道题目后，会发现博学者颤抖了一下；回答对第二道题，博学者会惊讶的说</span><span class="rvts20"></span><span class="rvts21">我的老天爷啊，我真不敢相信！（</span><span class="rvts20">Oh, oh Gods! I... I can hardly bear to...</span><span class="rvts21">）</span><span class="rvts20"></span><span class="rvts21">；回答对第三道题后，他会说</span><span class="rvts20"></span><span class="rvts21">哎呀呀，我受够了这种所谓的生存烦恼！（</span><span class="rvts20">Nnnnnnggghhh! ... I can't bear this... this existential agony</span><span class="rvts21">）</span><span class="rvts20"></span><span class="rvts21">便会自爆成为骨堆，之后便可以在他身上获得</span><span class="rvts20"></span><span class="rvts21">超级尸爆（</span><span class="rvts20">Mass Corpse Explosion</span><span class="rvts21">）</span><span class="rvts20"></span><span class="rvts21">技能书。</span>
<br><span class="rvts21">当然，如果玩家在回答问题之前选择杀掉了这个博学者，也可以获得超级尸爆技能书。</span>
<br><span class="rvts39"></span>
<br><span class="rvts21">《生命的本质》一共有三卷，全部分散在瑞克的洋馆内，偷出这些书，或杀掉瑞克，可以自己权衡利弊，三卷书籍的位置具体如下。</span>
<br><img width="100%" height="auto" alt="" hspace="1" vspace="1" src="../../../assets/pic2/abcamber1237.jpg" display="block"><span class="rvts39"></span>
<br><span class="rvts21">《生命的本质：第一卷》的位置。</span>
<br><span class="rvts39"></span>
<br><img width="100%" height="auto" alt="" hspace="1" vspace="1" src="../../../assets/pic2/abcamber1238.jpg" display="block"><span class="rvts39"></span>
<br><span class="rvts21">《生命的本质：第二卷》的位置。</span>
<br><span class="rvts39"></span>
<br><img width="100%" height="auto" alt="" hspace="1" vspace="1" src="../../../assets/pic2/abcamber1239.jpg" display="block"><span class="rvts39"></span>
<br><span class="rvts21">《生命的本质：第三卷》的位置。</span></p>
<p><br></p>
<p><br></p>

    </div>
  </div>
</template>
<script>
import BScroll from 'better-scroll'
export default {
  data () {
    return { clickcount : 1  };
  },
  methods: {
    _initScroll() {
      this.popScroll = new BScroll(this.$refs.popWrapper, {
        click: true,
        probeType: 3
      });
    },
    timer(){
      if (this.clickcount < 3) {
        this.popScroll.refresh()
        this.clickcount = this.clickcount + 1
      }
    }
  },
  created() {
    this.$nextTick(() => {
      this._initScroll();
    });
    setInterval(this.timer, 2000);
  }
};
</script>
<style>
@import '../../../../static/css/common.css';
</style>



